
#app {
  font-family: 'Microsoft YaHei', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #666;
  margin: 10px auto;
  padding: 10px 30px;
}

.catalog{
	height: 70px;
}
.catalog .cata-title{
	color: #288bde;
}
.catalog ul{
	margin-top: 0;
	padding: 10px 0px;
	list-style: none;
}
.catalog ul li{
	width: 100px;
	height: 26px;
	text-align: center;
	line-height: 26px;
	border: 2px solid #eee;
	background-color: #eee;
	float: left;
	margin-right: 10px;
	cursor: pointer;
	border-radius: 4px;
}
.catalog li.active{
	background-color: #fff;
	border: 2px solid #288bde;
}

.list{
	float: left;
	width: 300px;
	height: 400px;
	border: 2px solid #288bde;
	border-radius: 4px;
}
.list .list-caption{
	height: 40px;
	line-height: 40px;
	font-size: 20px;
	color: #fff;
	background-color: #288bde;
	text-align: center;
}
.tb{
	border: none;
	width: 100%;
}
.tb td, .tb th{
	text-align: center;
	color: #666;
	font-size: 14px;
}
.tb th{
	font-weight: normal;
	height: 25px;
	line-height: 20px;
	background-color: #eee;
}
.tb tbody tr.tractive{
	background-color: #e9f3fc;
}
.tb td{
	height: 35px;
	line-height: 35px;
}
.tb td:nth-of-type(1){
	width: 20%;
}
.tb td:nth-of-type(2){
	width: 30%;
	max-width: 90px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.tb td:nth-of-type(3){
	width: 16%;
}
.tb td:nth-of-type(4){
	width: 16%;
}
.tb td:nth-of-type(5){
	width: 16%;
}
.tb td{
	border-bottom: 1px solid #ddd;
}

.goods{
	float: left;
	width: 500px;
	height: 600px;
	border: 2px solid #eee;
	border-radius: 4px;
	margin-left: 10px;
}
.des{
	height: 40px;
	line-height: 40px;
	padding-left: 10px;
	font-size: 16px;
	color: #288bde;
	background-color: #eee;
}
.g-btn{
	display: inline-block;
	width: 80px;
	padding: 6px 12px;
	margin-bottom: 0;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.428;
	text-align: center;
	color: #999;
	background-color: #eee;
	white-space: nowrap;
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	border-radius: 4px;
	border: 1px solid transparent;
}
.g-btn:hover{
	color: #fff;
	background-color: #288bde;
}

.goods-child{
	padding: 10px;
	width: 96%;
	height: 100px;
	border-bottom: 1px solid #ddd;
}
.g-img{
	width: 100px;
	height: 100px;
	border: 1px solid #000;
	float: left;
}
.g-content{
	width: 350px;
	height: 100px;
	float: right;
	margin: 10px;
	border: 1px solid #fff;
	position: relative;
	left: 0;
	top: 0;
}
.g-name{
	display: block;
	cursor: pointer;
}
.g-name:link, .g-name:hover{
	color: orange;
}
.g-detail{
	font-size: 14px;
	color: #288bde;
	cursor: pointer;
}
.g-price{
	display: inline-block;
	margin: 0;
	position: absolute;
	right: 30px;
	top: 20px;
	font-size: 20px;
	color: red;
}
